import { PageProps } from "vendor/laravel/breeze/stubs/inertia-react-ts/resources/js/types";

import { useEffect, useState } from "react";

import { Head, usePage } from "@inertiajs/react";

import Footer from "@/Components/Footer";
import Navbar from "@/Components/Navbar";
import BuktiPengerjaan from "@/Components/mading/BuktiPengerjaan";
import CompletedMessage from "@/Components/mading/CompletedMessage";
import RiwayatTugas from "@/Components/mading/RiwayatTugas";
// import { Button } from "@/Components/ui/button";
import { Button } from "@/Components/ui/button";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
} from "@/Components/ui/carousel";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/Components/ui/dialog";
import { Toaster } from "@/Components/ui/toaster";
import { toast } from "@/Components/ui/use-toast";

import { TaskSystem } from "@/lib/types/Mading";
import { UserAuthProps } from "@/lib/types/User";

import awan from "!assets/awan.png";
import contohCover from "!assets/contoh_cover.png";
import gunung from "!assets/gunung.png";
import kotakajaib from "!assets/kotakajaib.png";
import lampu from "!assets/mading/lampu.png";
import pentung from "!assets/pentung.png";
import piano from "!assets/piano.png";

const SkeletonLoader = () => {
  return (
    <Carousel
      opts={{
        align: "start",
      }}
      className="scroll-smooth max-w-[1920px] xl:pl-36 lg:pl-20 md:pl-10 w-full pl-5 mx-auto mt-10"
    >
      <CarouselContent>
        {[...Array(6)].map((_, index) => (
          <CarouselItem
            key={index}
            className="basis-72 md:basis-80 xl:basis-[26rem]"
          >
            <div className="xl:w-[400px] md:w-[300px] lg:w-[310px] md:h-[550px] w-[280px] h-[500px] xl:h-[600px] overflow-hidden rounded-lg relative border border-dashed border-gray-300 animate-pulse bg-gray-200">
              <div className="flex flex-col items-center justify-center p-4">
                <div className="w-2/3 h-10 mt-5 bg-gray-300 rounded"></div>
                <div className="w-32 h-32 mt-32 bg-gray-300 rounded-full"></div>
                <div className="mt-36 w-1/3 h-10 bg-gray-300 rounded"></div>
              </div>
            </div>
          </CarouselItem>
        ))}
      </CarouselContent>
    </Carousel>
  );
};

const Mading = ({ succes }: { succes?: string }) => {
  const ketentuanUploadCover = [
    "Ketua Kelompok mengupload cover jika persentase pada Day telah mencapai 100%",
    "Cover harus berbentuk kolase foto, seperti kolase kumpulan tugas, kolase foto saat kalian bekerja dalam kelompok, dan sebagainya.",
    "Desain cover bebas, sesuai dengan kreativitas kelompok.",
    "Terdapat deskripsi singkat pada gambar cover",
    "Kalian dapat membuat cover menggunakan Canva, Figma, atau aplikasi editing lain.",
    "Pastikan ukuran cover adalah 700x1000 piksel.",
    "Pastikan kalian membuat cover yang sesuai, tidak boleh mengandung SARA, serta harus sopan dan mencerminkan nilai-nilai yang positif.",
    "Contoh cover seperti dibawah ini. Semangat Naramuda!",
  ];
  type MyPage = PageProps<{
    auth: {
      user: UserAuthProps;
    };
  }>;

  const { auth } = usePage<MyPage>().props;

  const [data, setData] = useState<TaskSystem | null>(null);

  const getCard = async () => {
    try {
      const response = await fetch(route("mading.card"), {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (!response.ok) {
        throw new Error("HTTP status " + response.status);
      }

      const data: TaskSystem = await response.json();
      //console.log(data);
      setData(data);
    } catch (error) { }
  };

  useEffect(() => {
    getCard();
    console.log(auth.user);
  }, []);

  return (
    <div>
      <Head title="Mading" />

      <Navbar isSolid={true} isFixed={true} />

      <div className="bg-pattern-white md:pt-36 flex flex-col items-center justify-center min-h-screen pt-20 pb-20">
        <div className="flex flex-col items-center justify-center px-5">
          <h2
            data-aos="fade-down"
            data-aos-duration="1000"
            className="font-greek text-center text-jaffa-800 w-fit mx-auto pt-[30px] text-3xl md:text-5xl"
          >
            Mading Tugas PPLK 2025
          </h2>

          <div className="relative xl:max-h-[186px] md:max-w-[600px] xl:max-w-[800px] w-full h-full rounded-xl bg-history-completed bg-no-repeat bg-transparent bg-cover mt-14 p-4">
            <div className="left-4 top-4 border-jaffa-800 absolute p-2 border rounded-full shadow-sm">
              <img src={piano} alt="Piano" className="w-6 h-6" />
            </div>

            <div className="md:ml-20 ml-0">
              <h2 className="font-montserrat ml-16 md:ml-0 text-[20px] md:text-[25px] text-jaffa-800 font-bold mb-2">
                Apa Itu Mading Tugas PPLK?
              </h2>

              <p className="text-[20px] text-sm text-justify text-black font-montserrat mb-2">
                Mading Tugas PPLK adalah sebuah media yang digunakan untuk
                menampilkan hasil pengerjaan tugas PPLK 2024. Mading ini berisi
                bukti pengerjaan tugas yang telah dikerjakan oleh kelompok
                masing-masing. Segera selesaikan tugas yang diberikan untuk
                membuka mading kelompok kamu!.
              </p>
            </div>
          </div>
        </div>
        {!!auth.user.isKetua ? (
          <div className="md:mt-10 rounded bg-white border border-r-amber-100 shadow p-5 md:max-w-[800px]">
            <h2 className="text-jaffa-700 font-montserrat text-xl font-bold">
              Cek Ketentuan Cover Mading
            </h2>

            <div className="mt-5">
              {ketentuanUploadCover.map((ketentuan, index) => (
                <div key={index} className="flex gap-2">
                  <p>{index + 1}.</p>
                  <p>{ketentuan}</p>
                </div>
              ))}
            </div>
            <Dialog>
              <DialogTrigger asChild>
                <Button className="bg-jaffa-700 hover:bg-jaffa-700/90 mt-5 transition duration-200">
                  Contoh Cover Mading
                </Button>
              </DialogTrigger>
              <DialogContent className="sm:max-w-[425px] h-fit">
                <DialogHeader>
                  <DialogTitle>Contoh Cover Mading</DialogTitle>
                  <DialogDescription>
                    Berikut adalah contoh cover mading yang baik dan benar.
                  </DialogDescription>
                </DialogHeader>
                <div className="flex justify-center">
                  <img src={contohCover} className="w-[85%]" />
                </div>
              </DialogContent>
            </Dialog>
          </div>
        ) : (
          <></>
        )}
        <div className="relative z-10 w-full">
          <div className="flex flex-col justify-center mt-24">
            <h2 className="font-greek md:text-[25px] mx-auto md:max-w-[600px] xl:max-w-[800px] max-w-[280px] text-[20px] font-bold text-jaffa-800 text-center">
              Kumpulkan bukti pengerjaan tugas dibawah ini
            </h2>

            {!!data ? (
              <BuktiPengerjaan data={data} isKetua={auth.user.isKetua} />
            ) : (
              <SkeletonLoader />
            )}
          </div>
        </div>

        {data?.isSelesai ? (
          <CompletedMessage />
        ) : (
          <>{data?.history && <RiwayatTugas historys={data.history} />}</>
        )}
      </div>

      <div className="bg-pattern-white py-10 lg:py-52 relative bg-[#170C0A] flex flex-col gap-10 items-center justify-center">
        <div className="absolute top-0 flex items-center justify-center md:-translate-y-[80px] lg:-translate-y-[100px] -translate-y-[30px]">
          <img src={awan} alt="" className="w-full" />
        </div>

        {data?.isSelesai ? (
          <>
            <h2 className="text-jaffa-50 mt-5 xl:mt-20 font-greek font-bold md:text-[25px] text-[20px] xl:text-[60px] z-20">
              MADING TUGAS SELESAI
            </h2>

            <img src={lampu} alt="lampu" className="absolute top-0 z-10" />

            <img
              src={kotakajaib}
              alt="kotakajaib"
              className="xl:scale-100 md:mt-0 z-20 -mt-20 scale-50"
            />

            <a
              href={route("mading.preview")}
              className={`z-20 xl:mt-32 -mt-10 cursor-pointer text-sm text-white bg-jaffa-800 hover:bg-jaffa-800 shadow-md inline-flex items-center justify-center whitespace-nowrap rounded-md font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 px-4 py-2`}
            >
              Lihat Preview Mading Kelompokmu
            </a>
          </>
        ) : (
          <>
            <div className="flex flex-col items-center gap-5 p-4 rounded-lg">
              <img src={pentung} alt="Pentung" className="w-[19px] h-[44px]" />

              <h2 className="lg:text-[25px] md:text-[20px] text-sm text-center font-montserrat text-white">
                Selesaikan semua tugas yang diberikan untuk <br />
                mendapatkan hasil Mading dibawah ini
              </h2>
            </div>

            <h2 className="text-jaffa-50 mt-5 font-greek font-bold md:text-[25px] text-[20px] xl:text-[60px]">
              MADING TUGAS
            </h2>

            <img
              src={kotakajaib}
              alt=""
              className="md:scale-75 xl:scale-100 scale-75"
            />

            <a
              href={route("mading.preview")}
              className={`z-20 xl:mt-32 -mt-10 cursor-pointer text-sm text-white bg-jaffa-800 hover:bg-jaffa-800 shadow-md inline-flex items-center justify-center whitespace-nowrap rounded-md font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 px-4 py-2`}
            >
              Lihat Preview Mading Kelompokmu
            </a>

            <div className="md:pb-96 xl:pb-[500px] pb-32" />

            <img src={gunung} alt="" className="absolute bottom-0 w-full" />
          </>
        )}
      </div>

      <Toaster />
      <Footer />
    </div>
  );
};

export default Mading;
